function pageTransition(){
  console.log('transitioning...')
  var tl = gsap.timeline();
  tl.to('.navigation', 0.4, {opacity: 0, autoAlpha: 0, ease: Power2.easeIn}, "<")
  .to('.slideshow-title', 0.4, {opacity: 0, ease: Power2.easeIn}, "<")
  .to('.slideshow-adjust', 0.4, {opacity: 0, autoAlpha: 0, ease: Power2.easeIn}, "<")
  .to('.colors', 0.4, {opacity: 0, autoAlpha: 0, ease: Power2.easeIn}, "<")
}
function contentAnimation(){
  var tl = gsap.timeline();
  tl.to('.slide__figure-img', 0.5, {y: window.innerHeight})
}
function delay(n){
  n = n || 2000;
  return new Promise(done => {
    setTimeout(()=>{
      done();
    }, n)
  });
}

barba.init({
  sync: true,
  transitions: [{
    name: 'basic-transition',
    from: {
      namespace: ['home']
    },
    to: {
      namespace: ['benjamin']
    },
    async leave(data){
      const done = this.async();

      pageTransition();
      await delay(1500);
      done();
    },
    async enter(data){
      contentAnimation();
    },
    async once(data){
      contentAnimation();
    }
  }]
})
